.particuliers{
    display: flex;
    flex-direction: column;
    font-size: 30px;
    align-items: center;
    padding: 30px 0;
    .h2title{
        color: $color-1;
    }
    .iconExplose{
        display: flex;
        align-items: center;
        flex-direction: column;
        .title{
            color: $color-1; 
        }
        .iconCharge{
            width: 750px;
            height: 500px;
                img{
                    width: 100%;
                    object-fit: cover;
                }
        }
        img{
            width: 150px 
        }
    }
    .iconMedit{
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 586px;
    }
    .title{
        padding: 2rem;
    }
    .h2title{
        display: inline;
        padding: 2rem;
    }
    .h2titleIcon{
        color: $color-2;
        display: flex;
        padding: 0rem 2rem 2rem 2rem;
    }
    .particuliersListe{
        margin: 1rem;
        font-size: 25px;
        list-style-type: disclosure-closed;
        li{
            padding: 0.2rem;
            &::marker{
                color: $color-1;
            }
        }
    }

}
@media (max-width:425px){
    .particuliers{
        .iconExplose{
            .title{
                font-size: 50px;
                padding: 1rem;
            }
            .iconCharge{
                width: auto;
                height: auto; 
                img{
                    height: auto;  
                    width: 100%;
                }
            }
        }
        .h2title{
            padding: 1rem; 
        }
        .iconMedit{
            img{
                height: 420px;
                width: 100%;
            }
        }
        .particuliersListe{
            padding: 1rem;
        }
    }
}