@import './colors';

.home{
    width: 100%;
    margin: 0 auto;
    .bgCard{
        display: flex;
        // flex-direction: column;
        padding: 4% 0;
    }
    .wordContain{
        color: #C7AAA3;
        padding-left: 10px;
    }
    .barre{
        border-right: 1px solid black;
    }
    .textAnimContain{
        display: flex;
        width: 100%;
        .backImg{
            width: 50%;
            img{
                object-fit: cover;
                width: 100%;
                height: 70vh;
            }
        }
        .text{
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 50%;
            text-align: center;
            color: #F7F3F5;
            background-color: #403234;
            text-shadow: 3px 0px black;
            height: 70vh;
            font-size: 45px;           
            h4{
                padding-bottom: 80px;
                font-weight: 600;
            }
        }
    }
}

@media (max-width:1024px) {
    .home {
        .bgCard{
            .card {
                height: 280px;
                margin: 2%; 
            }
        }
        .twoCard {
            .twoFlex{
                width: 300px;  
            }
        }
    }
}  
@media (max-width:834px){ 
    .home{
        .textAnimContain{
            flex-direction: column; 
            .backImg{
                width: 100%;
                height: 45vh;
                img{
                    height: 45vh;
                }
            }
            .text{
                width: 100%;
                height: 45vh; 
                font-size: 30px;
            }
        }
    }
}
@media (max-width:425px) {
    .home {
        .textAnimContain{
            .text{ 
                h4{
                    padding-bottom: 30px; 
                }
            }
        }
        .text{
            font-size: 25px;
        }
        .bgCard{
            flex-direction: column;
            .card{
                width: 70%;
            }
        }
        .twoCard{
            width: 70%;
        }
    }
}